.createGoals_md {
    display: flex;
}

.inventoryContainer {
    /* margin: 90px 30px; */
    margin: 10px auto;
    margin-bottom: 30px;
    width: 100%;
    max-width: 12500px;
    padding: 0px 15px;
}

.inventor_table {
    width: 100%;
}

.create_goals_box_main {
    margin-top: 30px;
}

.create_goals_box {
    background: white;
    padding: 20px;
    border: 1px solid gainsboro;
    border-radius: 5px;
    width: 80%;
    margin: 30px auto;
    position: relative;
}

.goals_inp {
    margin-top: 10px;
}

.goals_inp input,
.goals_inp textarea,
.goals_inp select {
    padding: 8px;
    border: none;
    outline: none;
    background-color: rgb(238, 237, 237);
    width: 100%;
}

.reward_inp span {
    /* width: 48%; */
    margin: 0px 5px;
}

.half_gaols_md {
    display: flex;
}

.goals_inp label {
    display: flex;
    align-items: center;
}

.decs_label {
    display: flex !important;
    align-items: flex-start !important;
}

.goals_inp p {
    max-width: 150px;
    min-width: 120px;
    font-weight: 600;
}

.goals_pic_upload {
    margin-top: 15px;
    width: 350px;
    text-align: center;
}

.goals_pic_upload>p {
    text-align: start !important;
}

.right_hald_inp {
    margin-left: 15px;
}

.addGoals_btn {
    text-align: end;
    margin-top: 20px;
}

.addGoals_btn button {
    border: none;
    outline: none;
    width: 140px;
}

.empty {
    height: 120px;
}

.uploaded_goal_img {
    width: 250px;
    height: 120px;
    margin: 10px auto;
}

.choosePhoto_btn {
    /* background: #; */
    background-color: rgb(57, 57, 199);
    color: white;
    padding: 10px 30px;
    border-radius: 5px;
    /* margin-top: 100px; */
    display: inline-block;
}

.addGoals_loading {
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: rgba(43, 42, 42, 0.363);
    /* z-index: 9999; */
    top: 0px;
    left: 0px;
}

.addGoals_loading_md {
    display: flex;
    justify-content: center;
    align-items: center;
    transform: translate(00%, 250px);
}

.crossImg {
    position: relative;
    width: fit-content;
}

.crosImg_btn {
    position: absolute;
    background: gainsboro;
    border: none;
    outline: none;
    border-radius: 50%;
    right: -10px;
    font-size: 1.3rem;
    padding: 0px 6px;
    top: -6px;
}

.cus_btnUpload {
    width: 200px;
    height: 140px;
    border: dotted;
    padding: 20px 0px;
    display: inline-block;
    margin-top: 10px;
}

.iconss {
    font-size: 2.5rem;
}

.cus_btn {
    background-color: black;
    color: white;
    padding: 7px 30px;
    border-radius: 5px;
    border: none;
    outline: none;
    margin: 15px 0px;
}

.icBtn {
    background-color: gainsboro;
    border: none;
    outline: none;
    padding: 3px 6px;
    border-radius: 4px;
    margin: 0px 5px;
}